@import "../node_modules/normalize.css/normalize.css";
* {
    box-sizing: border-box; }

html {
    height: 100%; }

body {
    height: 100%; }

#root {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-height: 100vh; }

.container {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    height: calc(100vh - 128px);
    padding: 0 15px; }

.loader {
    align-self: center;
    margin-top: auto;
    margin-bottom: auto; }

.select {
    width: 100%; }

.list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    overflow: auto;
    padding: 15px 0; }

ul {
    margin: 0;
    padding: 0; }

li {
    list-style: none; }
